import axios from 'axios';




const getAllNotes = async (session: any) => {

        let allNotes;
        try {
            /*    const response = await axios.post('http://localhost:8080/api/notes/create-note', 
                 { title: 'Untitled', content: '' },//content
                 {
                   headers: {
                     'Content-Type': 'application/json',
                     'Authorization': session.session.token, // Include the session token in the 'header'
                   },
                 }
               );
            */
            allNotes = await axios.get('http://localhost:8080/api/notes/get-all-notes ', {
                 headers: {
                   'Content-Type': 'application/json',
                   'Authorization': session.session.token,
                 },
               }
            )           
               // Update the UI to reflect the new note
             } catch (error) {
               console.error('Failed to create note', error);
             } 

    return (
        <div>

           {  allNotes?.data.map((value :any, index:number) => (
                 <div key={index}>
                 <h1>{value.title}</h1>
                 <p>{value.content}</p>
                 </div>
            ))}
            
        </div>
    )

}

export default getAllNotes